.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.PlayTheGame {
  margin: 1rem
}

.GameButtonDivs {
  margin: 1rem
}

.GameButton {
  color: #fff;
  background-color: #2164d3;
  font-size: 1rem;
  border: 0;
  border-radius: 10px;
  height: 35px;
  padding: 0 15px;
  cursor: pointer;
  box-sizing: border-box;
  margin: 20px 20px 20px 20px;
}

.backbutton {
  color: #fff;
  background-color: #ff0000;
  font-size: 1rem;
  border: 0;
  border-radius: 10px;
  height: 35px;
  padding: 0 15px;
  cursor: pointer;
  box-sizing: border-box;

}

.labelsGame {
  margin: 20px 20px 20px 20px;
  
}
